export default {
  name: "Footer",
  data() {
    return {
      scrollBtn: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  // created() {
  //   this.asyncData();
  // },

  methods: {
    handleScroll() {
      if (window.scrollY > 70) {
        this.scrollBtn = true;
      } else if (window.scrollY < 70) {
        this.scrollBtn = false;
      }
    },

    scrollTop() {
      window.scrollTo(0, 0);
    },
    // async asyncData() {
    //   const ip = await this.$axios.$get("/index/list-teacher");
    //   console.log(ip);
    // },
  },
};
