import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper';
import 'swiper/css/swiper.css';

export default {
  name: "ClientArea",
  components: {
    Swiper,
    SwiperSlide
  },
  directives: {
    swiper: directive
  },
  data() {
    return {
      teacherList:[],
      swiperOptions: {
        slidesPerView : 2,
        loop: true,
        speed: 1000,
        spaceBetween : 30,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        // Responsive breakpoints
        breakpoints: {
          1024:{
            slidesPerView : 2
          },
          768:{
            slidesPerView : 1
          },
          640:{
            slidesPerView : 1
          },
          320:{
            slidesPerView : 1
          }
        }
      }
    }
  },
  created(){
       this.getTeacherData();
  },
  methods:{
   async getTeacherData() {
    const teacher = await this.$axios.$get("/index/list-teacher");
     console.log(teacher);
     if(teacher['code']!=0) return;
     this.teacherList=teacher["data"];
   },
  }
}
